import React, {useState} from 'react';
import Axios from "axios";

function Album(props) {

    const [aphoto,setAPhoto]=useState('');
    const [a_name,setAName]=useState('');

    // const photo1 =process.env.REACT_APP_IMGURL;


    const fileUploadEvent = (e) => {
        //  3   
        if(e.target.files.length>3)
        {
            alert("   3 !");
            return;
        }

        const upload = new FormData();
        for(let i = 0;i<e.target.files.length;i++)
        {
            upload.append("upload",e.target.files[i]);
        }
        console.log(upload)
        //        
        Axios({
            method:'post',
            url:'/upload',
            data:upload,
            headers:{'Content-Type':'multipart/form-data'}
        }).then(res=>{
            setAPhoto(res.data);
        })
    }

    // submit    
    const onSubmitEvent = (e) => {

        //   ( action    )
        e.preventDefault();
        if(aphoto.length===0)
        {
            alert("  ~");
            return;
        }

        //  
        Axios.post("/insert",{aphoto,a_name})
            .then(res=>{
                alert("");
            })
    }
    return (
        <div>
            <div>
                {/*<img alt={''} src={`${photo1}8481acf0-ab9e-4629-a72b-b4badc68926b`}/>*/}
            </div>
            <input type='file' id={'file'} multiple
                   onChange={fileUploadEvent}/>
            <form onSubmit={onSubmitEvent}>
                <input type={'text'}
                       onChange={(e)=>setAName(e.target.value)}/>
                <button type={'submit'}>  ~</button>
            </form>
        </div>
    );
}

export default Album;